export default{
	props:{
		msglist:{
			type:Object,
			default:{}
		}
	},
	data(){
		return{
			list:this.msglist,
			userimg:"../../../../static/login/part5_picture2.png",
			showname:false,
			showsex:false,
			shownum:false,
			checkname:"",
			num:"",
			sexlist:[
				{
				value:"男",
				name:"男"
			    },
				{
				value:"女",
				name:"女"
				},
			],
			current:'',
		
		}
	},
	created(){
	  this.list.phone=this.hidden(this.list.phone,3,4);
	  this.list.sfz=this.hidden(this.list.sfz,4,4);
	  this.hiddenrz();
	  this.current=this.list.sex;
	  this.hiddensex();
	  
	},
	methods:{
		gorz(){
		 uni.navigateTo({
		     url: '../user-authentication/user-authentication'
		 });
		},
		hidden(str,frontLen,endLen) { 
										var start='';
										var str1=str.toString();
										for(var i = 0;i<frontLen;i++) {
											start+=str1[i];
										}
										var len = str1.length-frontLen-endLen;
										 var xing = '';
										 for (var i=0;i<len;i++) {
										 xing+='*';
						                  }
										  var ends=frontLen+len;
										  var end='';
										  for(var i=ends;i<str1.length;i++) {
											 end+=str1[i];
										  }
										return start+xing+end;	
								   },
		hiddenrz(){
			var rzzt=this.list.rzzt;
			if(rzzt==true){
				rzzt="实名认证";
			}else{
				rzzt="未认证";
			};
			this.list.rzzt=rzzt;
		},
		hiddensex(){
			var sex=this.list.sex;
			if(sex==0){
				sex="男"
			}else{
				sex="女"
			};
			this.list.sex=sex;
		},
		changeimg(){
			var that=this;
			uni.chooseImage({
				count:1,
				sizeType:"compressed",
				success:function(res){
					that.userimg=res.tempFilePaths;
				}
			})
		},
		changnameshow(){
			this.showname=true;
		},
	    changnameno(){
			this.showname=false;
			this.checkname="";
		},
	    changnameyes(){
			this.showname=false;
			this.list.wxname=this.checkname;
			this.checkname="";
		},
		changnumshow(ev){
			if(ev=="未定义"){
				this.list.wxnum=this.num;
				uni.showToast({
				    title: '绑定成功',
					icon:"none",
				    duration: 2000
				});
			}else{
			  this.shownum=true;	
			}
		},
		changnumno(){
			this.shownum=false;
		},
		changnumyes(){
			this.shownum=false;
			this.num=this.list.wxnum;
			uni.showToast({
			    title: '解绑成功',
				icon:"none",
			    duration: 2000
			});
			this.list.wxnum="未定义";
		},
	   	changsexshow(){
			this.showsex=true;
		},
		changsexno(){
			this.showsex=false;
		},
	    changsexyes(){
		     if(this.current==0){
				 this.list.sex="男"
			 }else{
				this.list.sex="女" 
			 };
			 uni.showToast({
			     title: '修改成功',
			 	icon:"none",
			     duration: 2000
			 });
			this.showsex=false;
		},
		radioChange(evt) {
		    for (let i = 0; i < this.sexlist.length; i++) {
		        if (this.sexlist[i].value === evt.target.value) {
		            this.current = i;
		            break;
		        }
		    }
		},
		changnewphone(){
		  uni.navigateTo({
		  	url:"./check-number"
		  })	
		},
	}
}
